.LaptopWrapper {
    width: 100vw;
    height: 100vh;
    position: relative;
}

.Laptop {
    position: absolute;
    top: 30px;
    left: 30px;
    width: 1500px;
    height: 1000px;
    background-size: cover;
}

.LaptopFrame {
    position: absolute;
    z-index: 999;
    width: 1500px;
    height: 1000px;
    pointer-events: none;
}

.LaptopScreen {
    overflow-x: hidden;
    overflow-y: scroll;
    position: absolute;
    bottom: 120px;
    left: 150px;
    right: 139px;
    top: 150px;
    display: flex;
    flex-direction: column;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.LaptopContainer {
    height: 100%;
    width: 98%;
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.Row {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}

.NavColum {
    width: 20vh;
}

.ContentColum {
    width: 80vh;
}

.InputFix {
    height: 3vh;
    width: 90%;
}

.BtnMargin {
    position: relative;
    height: 3vh;
    border-radius: 0.3vh;
    transition: all 0.2s linear;

    width: 90%;

    margin: 10px 10px 4vh 10px;

    text-align: center;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: bold;
    letter-spacing: 0.1vh;
    line-height: 3vh;
}


.Btn {
    position: relative;
    background-color: #322f58;
    height: 3vh;
    border-radius: 0.3vh;
    transition: all 0.2s linear;

    width: 90%;

    margin: 10px 10px 2vh 10px;

    text-align: center;
    color: white;
    font-size: 1vh;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: bold;
    letter-spacing: 0.1vh;
    line-height: 3vh;
}

.Btn:hover {
    border-radius: 0.5vh;
    background-color: #4c4881;
}

.Btn.Error {
    background-color: #c0392b !important;
}

.Btn.Error:hover {
    border-radius: 0.5vh;
    background-color: #e74c3c !important;
}

.SliderInput {
    width: 100%;
}

.LabelKey {
    font-size: 1.17em;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}

.LabelValue {
    font-size: 0.83em;
    margin-block-start: 1.67em;
    margin-block-end: 1.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}